@use 'abstracts' as *;
@use 'colors' as *;
@use 'responsive' as *;

.contact {
  height: 100%;
  padding-top: 15vh;
  background: linear-gradient(
      to right,
      rgba($bg-color-dark-plain, 0.8),
      rgba($bg-color-dark-plain, 0.6),
      rgba($bg-color-dark-plain, 0.4),
      rgba($bg-color-dark-plain, 0.6),
      rgba($bg-color-dark-plain, 0.8)
    ),
    url('../../public/img/contat_bg.jpg');
  background-size: cover;
  background-repeat: repeat;

  &-heading {
    font-family: $text-headings;
    font-weight: 400;
    animation: heading 2s ease-out 0.2s both;
    text-align: center;
    color: $primary-color-deep-orange;
    text-shadow: $text-shadow-heavy;
    display: flex;
    flex-direction: row;
    justify-content: center;

    @keyframes heading {
      0% {
        transform: translateY(-100vh);
      }
      100% {
        transform: translateY(0);
      }
    }

    &-span {
      color: $text-color-light;
      text-shadow: none;
    }

    @include screen(x-small) {
      font-size: 2rem !important;
      margin: auto;
      margin-bottom: 1em !important;
    }

    @include screen(small) {
      font-size: 2.5rem;
      margin: auto;
    }

    @include screen(medium) {
      font-size: 3rem;
      margin-bottom: 1em;
    }

    @include screen(large) {
      font-size: 3rem;
      margin-left: 0.5em;
    }
  }
}

.contact-form {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  border-radius: 10px;
  background-color: rgba($bg-color-light-md, 0.7);
  animation: form 3s ease-out 1s both;

  @include screen(small) {
    width: 80vw;
    height: 100%;
    margin: auto;
    padding: 0.5rem;
    padding-bottom: 2rem;
  }

  @include screen(medium) {
    width: 80vw;
    height: 100%;
    margin: auto;
  }

  @include screen(large) {
    width: 60vw;
    height: 100%;
    margin: auto;
  }

  &-label {
    font-size: 1.2rem;
    color: $text-color-dark;
    margin-bottom: 0.5rem;
    font-size: 1.5rem;

    @include screen(small) {
      font-size: 1rem;
    }
  }

  &-control {
    border: none;
    outline: none;
    margin-bottom: 1rem;
    height: 3rem;
    border-radius: 10px;
    padding-left: 1rem;
    font-size: 1.2rem;

    @include screen(small) {
      font-size: 1rem;
      height: 2rem;
    }

    &::placeholder {
      @include screen(small) {
        font-size: 0.8rem;
      }

      @include screen(medium) {
        font-size: 1.2rem;
      }
    }
  }

  &-btn {
    width: 100px;
    height: 40px;
    font-size: 1.2rem;
    box-shadow: $shadow-normal;
    border: none;
    border-radius: 10px;
    margin: auto;
    background-color: $primary-color-green;
    transition: all 200ms ease-in-out;
    opacity: 0.8;
    transform: scale(0.8);

    @include screen(small) {
      font-size: 1rem;
      height: 2rem;
    }

    @include screen(large) {
      &:hover {
        opacity: 1;
        transform: scale(1);
      }
    }
  }
}

@keyframes form {
  0% {transform: translateX(-200vw);}
  100% {transform: translateX(0);}
}

#message {
  height: 200px;
  padding: 1rem;
}

.spacer {
  font-size: 5rem;
  margin: auto;
}