@use 'abstracts' as *;
@use 'colors' as *;
@use 'responsive' as *;

.navmenu {
  position: fixed;
  width: 80vw;
  right: 0;

  &.open {
    z-index: 98;
  }

  &-ul {
    transition: all 500ms ease-in-out;

    @include screen(small) {
      background-color: rgba($primary-color-green, 0.5);
      width: 80vw;
      height: 100vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 80vw;
      height: 100vh;
      transform: translateX(100vw);
    }

    @include screen(medium) {
      background-color: rgba($primary-color-green, 0.5);
      width: 80vw;
      height: 100vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 80vw;
      height: 100vh;
      transform: translateX(100vw);
    }

    @include screen(large) {
      display: flex;
      flex-direction: row;
      z-index: 98;
      height: 5%;
      background-color: transparent;
      justify-content: flex-end;
      align-items: flex-start;
      transform: translateX(0);
    }

    &.open {
      position: absolute;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 80vw;
      height: 100vh;
      transform: translate(0, 0);
      transition: all 500ms ease-in-out;
      top: 0;
      right: 0;
      background-color: rgba($bg-color-dark-plain, 0.98);
    }

    &-li {
      padding: 0.5rem;
      transition: all 500ms ease-in-out;
      transition-delay: 500ms;
      
      @include screen(small) {
        transform: translateX(100vw);
        padding: 1rem;
      }

      @include screen(medium) {
        transform: translateX(100vw);
        padding: 2rem;
      }

      @include screen(large) {
        transform: translateX(0);
        padding: 0.5rem 1rem;
      }

      &.open {
        transform: translateX(0vw);
      }

      &-a {
        font-family: $text-headings;
        color: $primary-color-green;
        font-size: 1.7rem;
        text-shadow: $text-shadow-light;
        transition: all 200ms ease-in-out;
        display: flex;
        flex-direction: row;
        font-style: italic;
        letter-spacing: 3px;
        text-transform: lowercase;
        padding: 0rem;

        @include screen(large) {
          &::before {
            content: attr(alt);
            display: flex;
            align-items: center;
            justify-content: flex-end;
            position: absolute;
            letter-spacing: 3px;
            opacity: 1;
            font-size: 1.7rem;
          }

          &:hover {
            text-shadow: $text-shadow-heavy;

            &::before {
              position: absolute;
              transition: all 0s;
              transform: translateY(100%);
              opacity: 0;
            }
          }

          & span {
            transition: all 2s ease;
            transform: translateY(-30px);
            opacity: 0;
          }

          &:hover span {
            transition: all 0.2s ease;
            transform: translateY(0px);
            opacity: 1;
            color: $primary-color-green;

            @include screen(small) {
              opacity: 0;
              transform: translateY(-30px);
            }
          }

          &:hover span:nth-child(1) {
            transition-delay: 0.1s;
          }
          &:hover span:nth-child(2) {
            transition-delay: calc(0.1s * 2);
          }
          &:hover span:nth-child(3) {
            transition-delay: calc(0.1s * 3);
          }
          &:hover span:nth-child(4) {
            transition-delay: calc(0.1s * 4);
          }
          &:hover span:nth-child(5) {
            transition-delay: calc(0.1s * 4);
          }
          &:hover span:nth-child(6) {
            transition-delay: calc(0.1s * 5);
          }
          &:hover span:nth-child(7) {
            transition-delay: calc(0.1s * 6);
          }
          &:hover span:nth-child(8) {
            transition-delay: calc(0.1s * 7);
          }
        }
      }
    }
    & .active {
      text-shadow: $text-shadow-heavy;
      color: $primary-color-green;
      text-transform: uppercase;
      font-style: normal;
    }
  }
}

.hamburger {
  width: 35px;
  height: 35px;
  position: absolute;
  margin-top: 1%;
  right: 1%;
  background: none;
  border: none;
  transition: all 500ms ease-in-out;
  background-color: rgba($color: $bg-color-dark, $alpha: 0.3);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 4px;
  align-items: center;
  justify-content: center;
  box-shadow: 1px 2px 5px black;
  cursor: pointer;

  @include screen(large) {
    display: none;
  }

  &-item {
    width: 25px;
    height: 3px;
    position: absolute;
    background-color: rgba($text-color-light, 0.8);
    border-radius: 10px;
    transition: all 500ms ease-in-out;
    box-shadow: 1px 1px 3px black;

    &::before {
      content: '';
      position: absolute;
      top: 9px;
      width: 25px;
      height: 3px;
      background-color: rgba($text-color-light, 0.8);
      transition: all 500ms ease-in-out;
      border-radius: 10px;
      box-shadow: 1px 1px 3px black;
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 9px;
      width: 25px;
      height: 3px;
      background-color: rgba($text-color-light, 0.8);
      transition: all 500ms ease-in-out;
      border-radius: 10px;
      box-shadow: 1px 1px 3px black;
    }

    &.open {
      background-color: transparent;
      box-shadow: none;
      z-index: 99;
      transform: rotate(360deg);
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      &::before {
        transform: rotate(45deg) translate(-6px, -6px);
        background-color: rgba($text-color-light, 0.9);
        width: 30px;
        box-shadow: none;
      }

      &::after {
        transform: rotate(-45deg) translate(-7px, 6px);
        background-color: rgba($text-color-light, 0.9);
        width: 30px;
        box-shadow: none;
      }
    }
  }

  &.open {
    transform: rotate(360deg);
    background-color: rgba($primary-color-green, 0.3);
    z-index: 99;
    transform: scale(0.7);
    box-shadow: 1px 2px 5px black;
  }
}

@for $i from 1 through 4 {
  .navmenu-ul-li:nth-child(#{$i}) {
    transition-delay: ($i * 0.15s) + 0.15s;
  }
}
