@use 'abstracts' as *;
@use 'colors' as *;
@use 'responsive' as *;

.home {
  height: 100vh;
  padding-top: 15vh;
  overflow: hidden;
  align-items: center;
  text-align: center;
  background: linear-gradient(
      45deg,
      rgba($bg-color-dark, 0.9),
      rgba($bg-color-dark, 0.5),
      rgba($bg-color-dark, 0.2),
      rgba($bg-color-dark, 0.2),
      rgba($bg-color-dark, 0.5),
      rgba($bg-color-dark, 0.6)
    ),
    url('../../public/img/pexels-life-of-pix-7613.jpg');
  background-size: cover;

  &-heading {
    font-family: $text-headings;
    font-weight: 400;
    text-align: center;
    width: 90vw;
    margin: auto;
    font-size: 2.5rem;
    animation: heading 2s ease-out 0.2s both;
    margin-top: 10vh;
    text-shadow: 1px 2px 3px gray;

    @keyframes heading {
      0% {
        transform: translateY(-100vh);
      }
      100% {
        transform: translateY(0);
      }
    }

    @include screen(large) {
      text-align: right;
      width: 40vw;
      margin-right: 35%;
      font-size: 3rem;
      margin-top: 10vh;
    }

    @include screen(x-large) {
      font-size: 4rem;
    }
  }

  &-name {
    font-family: $text-headings;
    font-size: 2rem;
    font-weight: 300;
    text-shadow: 1px 2px 3px gray;
    text-align: center;
    width: 90vw;
    margin: auto;
    padding-bottom: 1rem;
    animation: name 2s ease-out 2s both;

    @keyframes name {
      0% {
        transform: translateX(200vh);
      }
      100% {
        transform: translateX(0);
      }
    }
    span {
      color: $primary-color-deep-orange;
      font-weight: 400;
      text-shadow: 1px 2px 3px black;
    }

    @include screen(large) {
      text-align: right;
      width: 40vw;
      margin-right: 35%;
      padding-bottom: 0.5rem;
      font-size: 2.5rem;
    }

    @include screen(x-large) {
      font-size: 3rem;
    }
  }

  &-descr {
    display: flexCenter(row);
    font-family: $text-body;
    font-weight: 300;
    text-align: center;
    width: 90vw;
    margin: auto;
    animation: descr 3s ease-out 4.5s both;
    text-shadow: 1px 2px 3px gray;

    @keyframes descr {
      0% {
        transform: translateX(-200vh);
      }
      100% {
        transform: translateX(0);
      }
    }

    @include screen(large) {
      text-align: right;
      width: 40vw;
      margin-right: 35%;
      font-size: 1.2rem;
    }

    @include screen(x-large) {
      font-size: 1.5rem;
    }
  }
}
