@use 'abstracts' as *;
@use 'colors' as *;
@use 'responsive' as *;

.social {
  position: fixed;
  top: 1em;
  left: 0.3em;
  display: flex;
  flex-direction: column;
  height: 100%;
  z-index: 2;
  width: 10px;

  @include screen(small) {
    transform: scale(0.7);
    top: -15%;
    z-index: 1;
    left: 0.05em;
  }

  a {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 8px;
    padding: 6px;
    transition: all 800ms ease-in-out;
    cursor: pointer;
    position: relative;
    margin-bottom: 8px;
    padding-left: 135px;
    transform: translateX(-165px);
    font-family: $text-headings;
    font-size: 1.2rem;
    color: $text-color-dark;

    &::before {
      content: '';
      background-color: $bg-color-light;
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 210px;
      opacity: 0.9;
      border-top-right-radius: 50px;
      border-bottom-right-radius: 50px;
      z-index: -1;
      transition: all 800ms ease-in-out;
      box-shadow: 2px 3px 5px black;
    }

    @include screen(large) {
      &:hover {
        transform: translateX(-60px);
        color: $text-color-dark;
        &::before {
          background-color: $bg-color-light-md;
        }

        i {
          -webkit-transform: rotateY(360deg);
          -moz-transform: rotateY(360deg);
          -ms-transform: rotateY(360deg);
          -o-transform: rotateY(360deg);
          transform: rotateY(360deg);
          margin-left: 5px;
        }

        .fa-facebook {
          color: #065fb5;
          text-shadow: 1px 2px 3px black;
        }

        .fa-instagram {
          background: radial-gradient(
            circle at 30% 107%,
            #fdf497 0%,
            #fdf497 5%,
            #fd5949 45%,
            #d6249f 60%,
            #285aeb 90%
          );
          -webkit-background-clip: text;
          background-clip: text;
          -webkit-text-fill-color: transparent;
          text-shadow: 1px 2px 3px black;
        }

        .fa-twitter {
          color: #065fb5;
          text-shadow: 1px 2px 3px black;
        }

        .fa-whatsapp {
          color: #25d366;
          text-shadow: 1px 2px 3px black;
        }
      }

      i {
        transition: all 700ms;
        font-size: 2rem;
        color: set-text-color($text-color-dark);
        margin-left: 5px;
      }
    }
  }
}
