@mixin screen($size) {
  @if $size == x-small {
    @media (max-width: 375px) {
      @content;
    }
  } @else if $size == small {
    @media (max-width: 576px) {
      @content;
    }
  } @else if $size == medium {
    @media (min-width: 576px) {
      @content;
    }
  } @else if $size == large {
    @media (min-width: 992px) {
      @content;
    }
  } @else if $size == x-large {
    @media (min-width: 1600px) {
      @content;
    }
  }
}
