@use 'abstracts' as *;
@use 'colors' as *;
@use 'responsive' as *;

.about {
  height: 100vh;
  padding-top: 5vh;
  overflow: hidden;
  right: 2rem;

  @include screen(small) {
    padding-top: 10vh;
    background: linear-gradient(
        45deg,
        rgba($bg-color-dark-plain, 1),
        rgba($bg-color-dark-plain, 0.9),
        rgba($bg-color-dark-plain, 0.8),
        rgba($bg-color-dark-plain, 0.9),
        rgba($bg-color-dark-plain, 1)
      ),
      url('../../public/img/about_me_bg_mobile.jpg');
    background-size: cover;
    background-repeat: repeat;
  }

  @include screen(medium) {
    padding-top: 10vh;
    background: linear-gradient(
        to right,
        rgba($bg-color-dark-plain, 0.9),
        rgba($bg-color-dark-plain, 0.7),
        rgba($bg-color-dark-plain, 0.6),
        rgba($bg-color-dark-plain, 0.7),
        rgba($bg-color-dark-plain, 0.9)
      ),
      url('../../public/img/about_me_bg_mobile.jpg');
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @include screen(large) {
    padding-top: 10vh;
    background: linear-gradient(
        to right,
        rgba($bg-color-dark, 0.9),
        rgba($bg-color-dark, 0.7),
        rgba($bg-color-dark, 0.6),
        rgba($bg-color-dark, 0.7),
        rgba($bg-color-dark, 0.9)
      ),
      url('../../public/img/about_me_bg_desktop_md.jpg');
    background-size: cover;
  }

  &-heading {
    font-family: $text-headings;
    font-weight: 400;
    animation: heading 2s ease-out 0.2s both;
    text-align: right;
    color: $primary-color-deep-orange;
    text-shadow: $text-shadow-heavy;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;

    @keyframes heading {
      0% {
        transform: translateY(-100vh);
      }
      100% {
        transform: translateY(0);
      }
    }

    &-span {
      color: $text-color-light;
      margin-right: 10px;
      text-shadow: none;
    }

    @include screen(x-small) {
      width: 90vw !important;
      font-size: 2rem !important;
      margin: auto;
      margin-bottom: 1.5em !important;
    }

    @include screen(small) {
      width: 80vw;
      font-size: 2.5rem;
      margin: auto;
      margin-bottom: 1.5em;
    }

    @include screen(medium) {
      font-size: 3rem;
      width: 60vw;
    }

    @include screen(large) {
      padding-top: 10vh;
      font-size: 3rem;
      width: 50vw;
      margin-left: 0.5em;
    }
  }

  &-paragraph {
    color: $text-color-light;
    font-family: $text-body;
    font-size: 1rem;
    display: flex;
    flex-direction: column;
    font-weight: 400;
    text-align: right;
    transition: all 300ms ease-in-out;
    animation: bio 3s ease-out 0.2s both;
    animation-delay: 2s;

    @keyframes bio {
      0% {
        transform: translateY(100vh);
      }
      100% {
        transform: translateY(0);
      }
    }

    &-span {
      margin-bottom: 1rem;

      i {
        &:hover {
          opacity: 0.7;
        }

        a {
          font-family: $text-body;
          font-weight: 500;
          color: $primary-color-deep-orange;

          @include screen(small) {
            text-shadow: $text-shadow-light;
          }
        }
      }
    }

    @include screen(x-small) {
      font-size: 1rem !important;
      width: 90vw !important;
      margin: auto;
      line-height: 1.5rem !important;
    }

    @include screen(small) {
      font-size: 1.2rem;
      width: 80vw;
      margin: auto;
      line-height: 1.7rem;
      text-shadow: $text-shadow-light;
    }

    @include screen(medium) {
      font-size: 1.5rem;
      width: 60vw;
      padding-left: 3rem;
      padding-top: 10vh;
      line-height: 2rem;
      text-shadow: $text-shadow-light;
    }

    @include screen(large) {
      font-size: 1.5rem;
      width: 50vw;
      color: $text-color-light;
      text-shadow: $text-shadow-light;
    }

    @include screen(x-large) {
      font-size: 1.5rem;
    }
  }
}
