@use 'abstracts' as *;
@use 'colors' as *;
@use 'responsive' as *;

.projects {
  height: 100%;
  background: linear-gradient(
      to right,
      rgba($bg-color-dark, 0.1),
      rgba($bg-color-dark, 0.8),
      rgba($bg-color-dark, 0.9),
      rgba($bg-color-dark, 0.8),
      rgba($bg-color-dark, 1)
    ),
    url('../../public/img/projects_bg.jpg');
  background-size: cover;
  background-repeat: repeat;

  @include screen(small) {
    padding-top: 10vh;
    padding-bottom: 1rem;
  }

  @include screen(medium) {
    padding-top: 10vh;
  }

  @include screen(large) {
    padding-top: 15vh;
  }

  &-heading {
    font-family: $text-headings;
    font-weight: 400;
    width: 70%;
    font-size: 2.5rem;
    animation: heading 2s ease-out 0.2s both;
    padding-bottom: 1rem;
    text-align: center;
    margin: auto;
    color: $text-color-light;
    display: flex;
    flex-direction: row;
    justify-content: center;

    @keyframes heading {
      0% {
        transform: translateY(-100vh);
      }
      100% {
        transform: translateY(0);
      }
    }

    @include screen(x-small) {
      font-size: 2rem !important;
    }

    @include screen(small) {
      font-size: 2.5rem;
      margin-bottom: 1rem;
    }

    @include screen(medium) {
      font-size: 3rem;
    }

    @include screen(large) {
      font-size: 3rem;
    }

    &-span {
      color: $primary-color-deep-orange;
      text-shadow: 1px 2px 3px black;
    }
  }
}

.projects {
  &-cards {
    margin: 2rem;
    animation: cards 5s ease-out 1s both;

    @keyframes cards {
      0% {transform: translateX(-200vw);}
      100% {transform: translateX(0);}
    }

    @include screen(small) {
      display: flex;
      flex-direction: column;
      gap: 1rem;
    }

    @include screen(medium) {
      display: flex;
      flex-wrap: wrap;
      gap: 1rem;
    }

    &-card {
      margin: auto;
      display: flex;
      flex-direction: column;
      margin: auto;
      padding: 2rem;
      background-color: $bg-color-light-md;
      border-radius: 20px;
      box-shadow: $shadow-normal;

      @include screen(small) {
        width: 75vw;
      }

      @include screen(medium) {
        width: 400px;
        height: 400px;
      }

      &-title {
        color: $text-color-dark;
        text-align: center;
        font-size: 1.4rem;
        margin-bottom: 1rem;
      }

      &-img {
        width: 100%;
        display: block;
        margin: auto;
        text-align: center;
        box-shadow: $shadow-normal;
        border-radius: 10px;
      }

      &-btns {
        text-align: center;
        padding-top: 1rem;
        display: flex;
        flex-direction: column;
        gap: 0.5rem;

        a {
          color: $text-color-light;
          font-size: 1.3rem;
          font-family: $text-body;
          background-color: $primary-color-deep-orange;
          padding: 0.5rem;
          width: 60%;
          margin: auto;
          border-radius: 20px;
          box-shadow: $shadow-normal;

          @include screen(small) {
            font-size: 1rem;
          }
        }
      }
    }
  }
}